import React , {useState} from 'react';
import Items from './Items';

const App=()=>{

  const [cval , uval] = useState();

  const [carr , uarr] = useState([]);

  const inputItem=(event)=>{
    uval(event.target.value);
  }

  const click=()=>{
    uarr((preVal)=>{
      return[...preVal , cval];
    });

    uval("");
  }

  return (
    <>
      <div className="main_div">
        <div className="box">
          <h1>TODO LIST</h1>
          <div className="row">
            <input type="text" placeHolder="Enter an item" onChange={inputItem} value={cval}/>
            <button className="btn_add" onClick={click}>+</button>
          </div>

          <ul>
            {carr.map((value , index )=>{
              return(
                <Items value={value}/>
              );
            })}
          </ul>
          
        </div>
      </div>
    </>
  );
}

export default App;