import React ,{ useState } from "react";

const Items=(props)=>{
    const [line , uline] = useState(false);

    const singleClick=()=>{
        uline(true)
      }

      const doubleClick=()=>{
        uline(false)
      }

    return(
        <>
            <li>
                <button className="btn_delete" onClick={singleClick} onDoubleClick={doubleClick}>x</button>
                <h2 style={{ textDecoration: line ? "line-through" : "none" }}>{props.value}</h2>
            </li>
        </>
    );
}

export default Items;